section {
  height: 100vh;
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 10vh 0 10vh 0;
  background-color: bisque;

  display: flex;
  align-items: center;
  justify-content: center;

  display: grid;
  grid-template-columns: 1fr 80% 1fr;
  grid-template-rows: min-content min-content 1fr 1fr;

  grid-column-gap: 0px;
  grid-row-gap: 0px;

  grid-template-areas:
    " . header . "
    " . text . "
    " . project01 . "
    " . project02 . ";

  h1 {
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
    font-size: clamp(1.5rem, 5vw, 4rem);
    font-weight: 800;
    letter-spacing: -0.07rem;
    text-align: center;
    padding: 10px 20px 10px 20px;
    grid-area: header;
  }

  span {
    grid-area: text;
    width: 100%;
    max-width: 1440px;
    font-size: clamp(12px, 1vw + 0.8rem, 40px);
    font-weight: 400;
    text-wrap: balance;
    text-align: center;
    padding: 20px 0 20px 0;
  }
}

app-project01 {
  grid-area: project01;
}

app-project02 {
  grid-area: project02;
}
